import React, { Fragment } from 'react';

import Header from '../../components/Header';
import Menu from '../../components/Menu';

import config from './config';

function Home() {
  return (
    <Fragment>

      <Header>
        <Header.Logo />
        <Header.Text>
          alanbsmith
        </Header.Text>
      </Header>
      <Menu config={config} />
    </Fragment>
  );
}

export default Home;



